/*
  MEDIA QUERY MANAGER
  -phone: 600px
  -tab-portrait: 900px
  -tab-landscape: 1200px 
  -big-desktop: 1800px
*/
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}
@media only screen and (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}
@media only screen and (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}
@media only screen and (max-width: 28rem) {
  html {
    font-size: 44%;
  }
}
@media only screen and (min-width: 112.5em) {
  html {
    font-size: 75%;
  }
}

/*Setting the general font props*/
body {
  box-sizing: border-box;
  padding: 3rem;
}
@media only screen and (max-width: 56.25em) {
  body {
    padding: 0;
  }
}

::selection {
  background-color: #55c57a;
  color: #fff;
}

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }
  80% {
    transform: translateX(1rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translateX(10rem);
  }
  80% {
    transform: translateX(-1rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(3rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /* font-size:16px; */
  line-height: 1.7;
  color: #777;
}

.primary-heading {
  text-transform: uppercase;
  color: #fff;
  backface-visibility: hidden;
  margin-bottom: 6rem;
  text-align: center;
}
.primary-heading--main {
  display: inline-block;
  font-size: 6rem;
  font-weight: 400;
  letter-spacing: 3.5rem;
  animation-name: moveInLeft;
  animation-duration: 2s;
  animation-timing-function: ease-out;
  /*
    animation-delay:3s;
    animation-iteration-count: 3;
    */
}
@media only screen and (max-width: 56.25em) {
  .primary-heading--main {
    font-size: 4.5rem;
    letter-spacing: 2.5rem;
    transform: translateX(3px);
  }
}
@media only screen and (max-width: 37.5em) {
  .primary-heading--main {
    font-size: 4rem;
    letter-spacing: 2rem;
  }
}
.primary-heading--sub {
  display: block;
  font-size: 2rem;
  font-weight: 600;
  letter-spacing: 1.75rem;
  animation-name: moveInRight;
  animation-duration: 2s;
  animation-timing-function: ease-out;
}
@media only screen and (max-width: 56.25em) {
  .primary-heading--sub {
    font-size: 2rem;
    letter-spacing: 0.8rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .primary-heading--sub {
    font-size: 1.3rem;
    letter-spacing: 1rem;
  }
}

.secondary-heading {
  color: #28b485;
  text-transform: uppercase;
  font-size: 3.5rem;
  font-weight: 700;
  display: inline-block;
  letter-spacing: 0.2rem;
  transition: all 0.2s;
}
@supports (-webkit-background-clip: text) or (background-clip: text) {
  .secondary-heading {
    background-image: linear-gradient(to right, #7ed56f, #28b485);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
  }
}
@media only screen and (max-width: 75em) {
  .secondary-heading {
    font-size: 2.5rem;
  }
}
@media only screen and (max-width: 56.25em) {
  .secondary-heading {
    font-size: 2rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .secondary-heading {
    font-size: 1.5rem;
  }
}
.secondary-heading:hover {
  transform: skew(5deg, 2deg) scale(1.1);
  text-shadow: 0.5rem 1rem 2rem rgba(0, 0, 0, 0.2);
}

.tertiary-heading {
  font-size: 1.6rem;
  font-weight: 700;
  text-transform: uppercase;
}

.paragraph {
  font-size: 1.5rem;
  text-align: justify;
}
.paragraph:not(:last-child) {
  margin-bottom: 3rem;
}

.u-center-text {
  text-align: center !important;
}

.u-mb-lg {
  margin-bottom: 8rem !important;
}
@media only screen and (max-width: 56.25em) {
  .u-mb-lg {
    margin-bottom: 5rem !important;
  }
}
@media only screen and (max-width: 37.5em) {
  .u-mb-lg {
    margin-bottom: 3rem !important;
  }
}

.u-mb-md {
  margin-bottom: 4rem !important;
}
@media only screen and (max-width: 56.25em) {
  .u-mb-md {
    margin-bottom: 3rem !important;
  }
}

.u-mb-sm {
  margin-bottom: 1.5rem !important;
}

.u-mt-lg {
  margin-top: 6rem !important;
}

.btn, .btn:link, .btn:visited {
  font-size: 1.6rem;
  text-transform: uppercase;
  text-decoration: none;
  padding: 1.5rem 4rem;
  display: inline-block;
  border-radius: 10rem;
  transition-property: all;
  transition-duration: 0.2s;
  position: relative;
  border: none;
}
.btn:hover {
  transform: translateY(-0.3rem);
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
}
.btn:hover::after {
  transform: scaleX(1.4) scaleY(1.5);
  opacity: 0;
}
.btn:active, .btn:focus {
  outline: none;
  transform: translateY(-0.1rem);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}
.btn::after {
  content: "";
  display: block;
  height: 100%;
  width: 100%;
  border-radius: 10rem;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: all 0.4s;
}
.btn__green {
  background-color: #55c57a;
  color: #fff;
}
.btn__green::after {
  background-color: #55c57a;
}
.btn__white {
  background-color: #fff;
  color: #777;
}
.btn__white::after {
  background-color: #fff;
}
.btn__white--animated {
  animation: moveInBottom 0.75s ease-out 0.75s backwards;
  /*animation-fill-mode: backwards;*/
}

.btn-learnMore:link, .btn-learnMore:visited {
  font-size: 1.6rem;
  text-decoration: none;
  display: inline-block;
  color: #55c57a;
  border-bottom: 1px solid #55c57a;
  padding: 3px;
  transition: all 0.2s;
}
.btn-learnMore:hover {
  background-color: #55c57a;
  color: #fff;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
  transform: translateY(-2px);
}
.btn-learnMore:active {
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
  transform: translateY(0);
}

.bg-video {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  opacity: 0.2;
}
.bg-video__content {
  width: 100%;
  height: 100%;
}
@supports (object-fit: cover) {
  .bg-video__content {
    object-fit: cover;
  }
}

.composition {
  position: relative;
}
.composition__photo {
  width: 55%;
  box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.4);
  border-radius: 2px;
  position: absolute;
  z-index: 10;
  transition: all 0.2s;
  outline-offset: 1.5rem;
}
@media only screen and (max-width: 56.25em) {
  .composition__photo {
    float: left;
    position: relative;
    width: 33.33333%;
  }
}
.composition__photo--p1 {
  left: 0;
  top: -2rem;
}
@media only screen and (max-width: 56.25em) {
  .composition__photo--p1 {
    left: 0;
    top: 1rem;
    transform: scale(1.1);
  }
}
.composition__photo--p2 {
  right: 0;
  top: 2rem;
}
@media only screen and (max-width: 56.25em) {
  .composition__photo--p2 {
    left: 0;
    top: -0.5rem;
    transform: scale(1.2);
    z-index: 100;
  }
}
.composition__photo--p3 {
  left: 20%;
  top: 10rem;
}
@media only screen and (max-width: 56.25em) {
  .composition__photo--p3 {
    left: 0;
    top: 1rem;
    transform: scale(1.1);
  }
}
.composition__photo:hover {
  outline: 1.5rem solid #55c57a;
  transform: scale(1.05);
  box-shadow: 0 2.5rem 4rem rgba(0, 0, 0, 0.5);
  z-index: 20;
}
.composition:hover .composition__photo:not(:hover) {
  transform: scale(0.85) translateY(-5rem);
}

.feature-box {
  background-color: rgba(255, 255, 255, 0.8);
  padding: 2.5rem;
  text-align: center;
  font-size: 1.5rem;
  border-radius: 3px;
  box-shadow: 0 1.5rem 2rem rgba(0, 0, 0, 0.2);
