.footer__link:hover {
  color: #55c57a;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15);
  transform: rotate(5deg) scale(1.3);
}
.footer__copyright {
  border-top: 1px solid #777;
  padding-top: 1rem;
  width: 80%;
}
@media only screen and (max-width: 56.25em) {
  .footer__copyright {
    width: 100%;
  }
}

.popup {
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 3000;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
}
@supports (backdrop-filter: blur(10px)) or (-webkit-backdrop-filter: blur(10px)) {
  .popup {
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background-color: rgba(0, 0, 0, 0.2);
  }
}
.popup__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 75%;
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.2);
  display: table;
}
@media only screen and (max-width: 75em) {
  .popup__content {
    width: 85%;
    display: block;
  }
}
.popup__content--left {
  width: 33.33333%;
  display: table-cell;
}
@media only screen and (max-width: 75em) {
  .popup__content--left {
    width: 100%;
    display: block;
    text-align: center;
  }
}
.popup__content--left img {
  display: block;
  width: 100%;
}
@media only screen and (max-width: 75em) {
  .popup__content--left img {
    display: inline-block;
    width: 49%;
  }
}
.popup__content--right {
  padding: 1.5rem 5rem;
  width: 66.666667%;
  display: table-cell;
  vertical-align: middle;
}
@media only screen and (max-width: 56.25em) {
  .popup__content--right {
    padding: 1.5rem 2rem;
  }
}
@media only screen and (max-width: 75em) {
  .popup__content--right {
    display: block;
    width: 100%;
  }
}
.popup__text {
  font-size: 1.4rem;
  margin-bottom: 2rem;
  column-count: 2;
  column-gap: 2rem;
  column-rule: 1px solid #eee;
  hyphens: auto;
}
@media only screen and (max-width: 75em) {
  .popup__text {
    font-size: 1.2rem;
    column-count: 2;
    column-gap: 1rem;
  }
}
.popup__content {
  transform: translate(-50%, -50%) scale(0.3);
  opacity: 0;
  transition: all 0.5s 0.3s;
}
.popup:target {
  opacity: 1;
  visibility: visible;
}
.popup:target .popup__content {
  transform: translate(-50%, -50%) scale(1);
  opacity: 1;
}
.popup__btn:link, .popup__btn:visited {
  font-size: 3.5rem;
  position: absolute;
  top: 2rem;
  right: 2rem;
  text-decoration: none;
  line-height: 1rem;
  color: #333;
}
.popup__btn:hover, .popup__btn:active {
  color: #55c57a;
}

.row {
  max-width: 114rem;
  margin: 0 auto;
}
.row::after {
  content: "";
  display: table;
  clear: both;
}
.row:not(:last-child) {
  margin-bottom: 8rem;
}
@media only screen and (max-width: 56.25em) {
  .row:not(:last-child) {
    margin-bottom: 6rem;
  }
}
@media only screen and (max-width: 56.25em) {
  .row {
    max-width: 55rem;
    padding: 0 3rem;
  }
}
.row [class^=col-] {
  float: left;
}
@media only screen and (max-width: 56.25em) {
  .row [class^=col-] {
    width: 100% !important;
  }
}
.row [class^=col-]:not(:last-child) {
  margin-right: 6rem;
}
@media only screen and (max-width: 56.25em) {
  .row [class^=col-]:not(:last-child) {
    margin-right: 0;
    margin-bottom: 6rem;
  }
}
.row .col-1-of-2 {
  width: calc((100% - 6rem) / 2);
}
.row .col-1-of-3 {
  width: calc((100% - (2 * 6rem)) / 3);
}
.row .col-2-of-3 {
  width: calc( 2 * ((100% - (2 * 6rem)) / 3) + 6rem );
}
.row .col-1-of-4 {
  width: calc((100% - (3 * 6rem)) / 4);
}
.row .col-2-of-4 {
  width: calc( 2 * ((100% - (3 * 6rem)) / 4) + 6rem );
}
.row .col-3-of-4 {
  width: calc( 3 * ((100% - (3 * 6rem)) / 4) + (2 *6rem) );
}